header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;

}
.header__container{

    text-align: center;
    height: 100%;
    position: relative;
}
/* CV*/
.cta{
   margin-top: 2.5rem;
   display: flex;
   gap: 1.2rem;
   justify-content: center;
   
}
/* socio icons*/
.header__socials{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    align-items: center;
    position: absolute;
    left: 0;
    bottom:3rem;
    
}
.header__socials::after{
    content:'';
    width: 1px;
    height: 2rem;
    background:var(--color-primary)

}
/* image me*/
.me{

    background: var(--color-primary);
    width:22rem;
    height: 30rem;
    position:absolute;
    left:50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    background: linear-gradient(var(--color-primary),transparent);

}
.scroll__down{
    position: absolute;
    right: -2.3rem;
   bottom: 5rem;
   transform: rotate(90deg);
   font-weight: 400;
   font-size: 0.9rem;

}
@media screen and (max-width:1024px)
{
 header{
    /* height: 68vh; */

 }

}

@media screen and (max-width:600px)
{
    header{
        height: 100vh;

    }
    .header__socials,.scroll__down
    {
        display: none;
    }

}